<template>
  <view class="container">
    <view class="header">
      <view class="title">ËæìÂÖ•Âá∫Áîü‰ø°ÊÅØ</view>
      <view class="subtitle">ËØ∑ÂáÜÁ°ÆÂ°´ÂÜôÂá∫ÁîüÊó∂Èó¥Ôºå‰ª•Ëé∑ÂæóÁ≤æÁ°ÆÁöÑÂàÜÊûêÁªìÊûú</view>
    </view>
    
    <!-- ÊÄßÂà´ÈÄâÊã© -->
    <view class="form-section">
      <view class="section-title">ÊÄßÂà´</view>
      <view class="gender-selector">
        <view 
          class="gender-item" 
          :class="{ active: form.gender === 'Áî∑' }"
          @tap="selectGender('Áî∑')"
        >
          <text class="gender-icon">üë®</text>
          <text class="gender-text">Áî∑</text>
        </view>
        <view 
          class="gender-item" 
          :class="{ active: form.gender === 'Â•≥' }"
          @tap="selectGender('Â•≥')"
        >
          <text class="gender-icon">üë©</text>
          <text class="gender-text">Â•≥</text>
        </view>
      </view>
    </view>
    
    <!-- ÂéÜÊ≥ïÈÄâÊã© -->
    <view class="form-section">
      <view class="section-title">ÂéÜÊ≥ï</view>
      <view class="calendar-selector">
        <view 
          class="calendar-item" 
          :class="{ active: !form.isLunar }"
          @tap="selectCalendar(false)"
        >
          <text class="calendar-text">ÂÖ¨ÂéÜ</text>
          <text class="calendar-desc">Èò≥ÂéÜ</text>
        </view>
        <view 
          class="calendar-item" 
          :class="{ active: form.isLunar }"
          @tap="selectCalendar(true)"
        >
          <text class="calendar-text">ÂÜúÂéÜ</text>
          <text class="calendar-desc">Èò¥ÂéÜ</text>
        </view>
      </view>
    </view>
    
    <!-- Âá∫ÁîüÊó•Êúü -->
    <view class="form-section">
      <view class="section-title">Âá∫ÁîüÊó•Êúü</view>
      <view class="date-selector" @tap="showDatePicker">
        <view class="date-display">
          <text class="date-text">{{ dateDisplayText }}</text>
          <text class="date-arrow">üìÖ</text>
        </view>
      </view>
    </view>
    
    <!-- Âá∫ÁîüÊó∂Èó¥ -->
    <view class="form-section">
      <view class="section-title">Âá∫ÁîüÊó∂Èó¥</view>
      <view class="time-selector" @tap="showTimePicker">
        <view class="time-display">
          <text class="time-text">{{ timeDisplayText }}</text>
          <text class="time-arrow">üïê</text>
        </view>
      </view>
      <view class="time-note">
        <text class="note-text">üí° Âá∫ÁîüÊó∂Èó¥ÂØπÁªìÊûúÂΩ±ÂìçÂæàÂ§ßÔºåËØ∑Â∞ΩÈáèÂáÜÁ°ÆÂ°´ÂÜô</text>
      </view>
    </view>
    
    <!-- Êèê‰∫§ÊåâÈíÆ -->
    <view class="submit-section">
      <button class="submit-btn" @tap="calculateBazi" :disabled="!isFormValid">
        ÂºÄÂßãÂàÜÊûê
      </button>
    </view>
    
    <!-- Êó•ÊúüÈÄâÊã©Âô® -->
    <picker
      v-if="showDateModal"
      mode="date"
      :value="form.date"
      @change="onDateChange"
      @cancel="showDateModal = false"
    >
      <view></view>
    </picker>
    
    <!-- Êó∂Èó¥ÈÄâÊã©Âô® -->
    <picker
      v-if="showTimeModal"
      mode="time"
      :value="form.time"
      @change="onTimeChange"
      @cancel="showTimeModal = false"
    >
      <view></view>
    </picker>
  </view>
</template>

<script>
import BaziCalculator from '@/utils/bazi-calculator.js';

export default {
  data() {
    return {
      form: {
        gender: 'Áî∑',
        isLunar: false,
        date: '',
        time: '',
        year: new Date().getFullYear(),
        month: new Date().getMonth() + 1,
        day: new Date().getDate(),
        hour: 12,
        minute: 0
      },
      showDateModal: false,
      showTimeModal: false,
      loading: false
    };
  },
  
  computed: {
    dateDisplayText() {
      if (!this.form.date) {
        return 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó•Êúü';
      }
      const date = new Date(this.form.date);
      return `${date.getFullYear()}Âπ¥${date.getMonth() + 1}Êúà${date.getDate()}Êó•`;
    },
    
    timeDisplayText() {
      if (!this.form.time) {
        return 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó∂Èó¥';
      }
      const [hour, minute] = this.form.time.split(':');
      return `${hour}Êó∂${minute}ÂàÜ (${this.getShiChenName(parseInt(hour))})`;
    },
    
    isFormValid() {
      return this.form.gender && this.form.date && this.form.time;
    }
  },
  
  onLoad() {
    // ËÆæÁΩÆÈªòËÆ§Êó•Êúü‰∏∫‰ªäÂ§©
    const now = new Date();
    this.form.date = now.toISOString().split('T')[0];
    this.form.time = '12:00';
  },
  
  methods: {
    selectGender(gender) {
      this.form.gender = gender;
    },
    
    selectCalendar(isLunar) {
      this.form.isLunar = isLunar;
    },
    
    showDatePicker() {
      this.showDateModal = true;
    },
    
    showTimePicker() {
      this.showTimeModal = true;
    },
    
    onDateChange(e) {
      this.form.date = e.detail.value;
      this.showDateModal = false;
    },
    
    onTimeChange(e) {
      this.form.time = e.detail.value;
      this.showTimeModal = false;
    },
    
    getShiChenName(hour) {
      const shiChen = [
        'Â≠êÊó∂', '‰∏ëÊó∂', 'ÂØÖÊó∂', 'ÂçØÊó∂', 'Ëæ∞Êó∂', 'Â∑≥Êó∂',
        'ÂçàÊó∂', 'Êú™Êó∂', 'Áî≥Êó∂', 'ÈÖâÊó∂', 'ÊàåÊó∂', '‰∫•Êó∂'
      ];
      const index = Math.floor((hour + 1) / 2) % 12;
      return shiChen[index];
    },
    
    async calculateBazi() {
      if (!this.isFormValid) {
        uni.showToast({
          title: 'ËØ∑ÂÆåÂñÑ‰ø°ÊÅØ',
          icon: 'none'
        });
        return;
      }
      
      this.loading = true;
      
      try {
        // ÊûÑÂª∫ÂÆåÊï¥ÁöÑÂá∫ÁîüÊó•ÊúüÊó∂Èó¥
        const [hour, minute] = this.form.time.split(':');
        const birthDateTime = new Date(`${this.form.date}T${this.form.time}:00`);
        
        // ËÆ°ÁÆóÂÖ´Â≠ó
        const baziResult = BaziCalculator.calculateBazi(
          birthDateTime,
          this.form.gender,
          this.form.isLunar
        );
        
        // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
        this.saveBaziHistory(baziResult);
        
        // Ë∑≥ËΩ¨Âà∞ÁªìÊûúÈ°µÈù¢
        uni.navigateTo({
          url: `/pages/result/result?data=${encodeURIComponent(JSON.stringify(baziResult))}`
        });
        
      } catch (error) {
        console.error('ËÆ°ÁÆóÂÖ´Â≠óÂ§±Ë¥•:', error);
        uni.showToast({
          title: 'ËÆ°ÁÆóÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        this.loading = false;
      }
    },
    
    saveBaziHistory(baziResult) {
      try {
        let history = uni.getStorageSync('bazi_history') || [];
        
        // Ê∑ªÂä†Êó∂Èó¥Êà≥ÂíåID
        const record = {
          id: Date.now(),
          timestamp: new Date().toISOString(),
          data: baziResult
        };
        
        history.unshift(record);
        
        // ÊúÄÂ§ö‰øùÂ≠ò20Êù°ËÆ∞ÂΩï
        if (history.length > 20) {
          history = history.slice(0, 20);
        }
        
        uni.setStorageSync('bazi_history', history);
      } catch (error) {
        console.error('‰øùÂ≠òÂéÜÂè≤ËÆ∞ÂΩïÂ§±Ë¥•:', error);
      }
    }
  }
};
</script>

